.account {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #c2cbd4;
    background-color: #fff;
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    padding: 1.5rem;
    box-sizing: border-box;
    text-align: left;
    margin-bottom: 2rem;

    &__content-wrapper {
        width: 100%;
        flex: 1;
    }

    &__title {
        margin: 0;
        padding: 0;
        font-size: 1rem;
        font-weight: normal;
    }

    &__amount {
        margin: 0;
        font-size: 2.5rem;
        font-weight: bold;
    }

    &__amount-description {
        margin: 0;
    }

    .transaction-button {
        display: block;
        width: 100%;
        padding: 8px;
        font-size: 1.1rem;
        font-weight: bold;
        margin-top: 1rem;
        border-color: #655ef2;
        background-color: #655ef2;
        color: #fff;
        border-radius: 5px;
    }
}

@media (min-width: 720px) {
    .account {
        flex-direction: row;

        &__content-wrapper--cta {
            flex: 0;

            .transaction-button {
                width: 200px;
                margin-top: 0;
            }
        }
    }
}